.catpet {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                          supported by Chrome and Opera */
  cursor: pointer; }

#catpet-menu li {
  margin: 0;
  padding: 0;
  line-height: normal;
  text-align: left; }

#catpet-menu {
  z-index: 999;
  width: 200px;
  margin: 0;
  padding: 10px;
  list-style: none !important;
  background: #fff;
  color: #333 !important;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px !important;
  -moz-box-shadow: 0 0 5px #999;
  -webkit-box-shadow: 0 0 5px #999;
  box-shadow: 0 0 5px #999 !important;
  font-family: Helvetica, Arial, sans-serif !important; }

#catpet-menu hr {
  border: 0;
  background: #d9d9d9;
  border-width: 0;
  color: #d9d9d9;
  height: 1px;
  margin-top: 5px;
  margin-bottom: 5px; }

#catpet-menu ul {
  list-style: none;
  padding: 0;
  margin: 0; }

#catpet-menu-header {
  padding-left: 7px;
  padding-right: 7px; }

#catpet-menu b {
  font-weight: bold;
  font-size: 12px !important;
  line-height: 15px; }

#catpet-menu ul li {
  padding: 7px !important;
  border-bottom: solid 1px #ccc !important;
  font-size: 14px !important;
  cursor: pointer !important;
  list-style: none; }

#catpet-menu ul li:hover {
  background: #EC6603 !important;
  color: #fff !important; }

#catpet-menu ul li:last-child, #catpet-menu ul li.last {
  border: none !important; }

#catpet-bubble, .catpet-bubble {
  /* aspect - 1.17820 */
  z-index: 999999999;
  opacity: 0.95;
  padding-bottom: 35px;
  background: url("chrome-extension://__MSG_@@extension_id__/images/bubble.svg");
  background-repeat: no-repeat !important;
  background-size: 100% !important;
  position: absolute !important;
  text-align: center;
  display: none;
  padding-left: 30px;
  padding-right: 30px;
  box-sizing: border-box !important;
  border: 0 !important;
  align-items: center;
  word-break: break-word;
  width: 230px;
  height: 200px; }

.catpet-bubble.bl, .catpet-bubble.br {
  padding-top: 75px; }

.catpet-bubble .reminder {
  color: red;
  font-size: 16px; }

.food.fish {
  background-image: url("chrome-extension://__MSG_@@extension_id__/images/icons/fish.png");
  background-size: 32px 32px;
  width: 32px;
  height: 32px; }

.food.cheese {
  background-image: url("chrome-extension://__MSG_@@extension_id__/images/icons/cheese.png");
  background-size: 32px 32px;
  width: 32px;
  height: 32px; }

.food.chicken {
  background-image: url("chrome-extension://__MSG_@@extension_id__/images/icons/chicken.png");
  background-size: 32px 32px;
  width: 32px;
  height: 32px; }

.catpet-bubble-text {
  font-weight: bold;
  font-size: 14px !important;
  color: #000 !important;
  direction: ltr !important;
  display: inline-block;
  margin: auto;
  font-family: Helvetica, Arial, sans-serif !important; }
